body {
  font-family: DM Sans, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --text-main-color: #000;
  --text-second-color: #fff;
  --text-footer-color: #545454;
  --text-error-color: #ff001b;
  --text-modal-color: #8c8c8c;
  --accent-color: #ff6b08;
  --header-bcg-color: rgba(0, 0, 0, .56);
  --main-bcg-color: #fff;
  --second-bcg-color: #f7f7f7;
  --backdrop-bcg-color: rgba(0, 0, 0, .25);
  --bcg-color-modal: #f7f7f7;
  --card-shadow: 0px 4px 4px rgba(0, 0, 0, .25);
  --border-radius: 5px;
  --transition-time: 300ms;
  --transition-function: inline;
}

.darkmode {
  --text-main-color: #fff;
  --text-second-color: #fff;
  --main-bcg-color: #000;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
  }
}

.section {
  padding-top: 20px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.page-navigation {
  width: 410px;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

@media screen and (max-width: 767px) {
  .page-navigation {
    width: 50vw;
    justify-content: space-between;
  }
}

.page-navigation > button {
  width: 40px;
  height: 40px;
  border-radius: var(--border-radius);
  cursor: pointer;
  background-color: var(--second-bcg-color);
  border: none;
}

@media screen and (max-width: 767px) {
  .page-navigation > button {
    width: 20vw;
    height: 10vh;
  }
}

.page-navigation span {
  text-align: center;
  padding-bottom: 8px;
}

.page-navigation-button__icon {
  fill-opacity: 0;
  stroke: #000;
}

@media screen and (max-width: 767px) {
  .page-navigation-button__icon {
    width: 10vw;
    height: 5vh;
  }
}

.page-navigation-button__icon--rotate {
  transform: rotate(180deg);
}

.pages-list {
  align-items: center;
  display: flex;
}

@media screen and (max-width: 767px) {
  .pages-list {
    display: none;
  }
}

.pages-list > li {
  width: 40px;
  height: 40px;
  background-color: var(--second-bcg-color);
  border-radius: var(--border-radius);
  cursor: pointer;
  letter-spacing: normal;
  justify-content: center;
  align-items: center;
  margin: 1px;
  padding: 10px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: flex;
}

.pages-list > li.active {
