  padding-bottom: 30px;
}

.footer__info {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .footer__info {
    justify-content: center;
    align-items: baseline;
    display: flex;
  }
}

@media screen and (max-width: 767px) {
  .footer__wrapper-rights {
