  text-decoration: underline;
}

.top-btn {
  z-index: 3;
  color: var(--text-second-color);
  transition: background-color var(--transition-time), var(--transition-function), opacity var(--transition-time), var(--transition-function);
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  background-color: #767d83;
  border: none;
  border-radius: 100px 0 0;
  outline: none;
  flex-direction: column;
  align-items: center;
  padding: 12px 12px 12px 25px;
  position: fixed;
  bottom: 0;
  right: 0;
}

.top-btn:hover {
  background-color: var(--accent-color);
}

.top-btn--is-show {
  opacity: 1;
  visibility: visible;
}

.top-btn__icon {
  width: 30px;
  fill: currentColor;
}

.gallery__image {
