.dark-mode-toggle {
  width: 46px;
  height: 46px;
  box-sizing: border-box;
  background: none;
  border: 1px solid;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 12px;
  display: flex;
  position: relative;
}

.sun {
  width: 50%;
  height: 50%;
  pointer-events: none;
  opacity: 0;
  background: radial-gradient(circle, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 50%, #f0f0f0 50%);
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  position: absolute;
  transform: scale(.6)rotate(0deg);
}

.sun:before {
  content: "";
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, #f0f0f0 30%, rgba(0, 0, 0, 0) 31%, rgba(0, 0, 0, 0) 50%, #f0f0f0 50%);
  display: block;
  position: absolute;
  transform: rotate(45deg);
}

.sun.visible {
  pointer-events: auto;
  opacity: 1;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  transform: scale(1)rotate(180deg);
}

.moon {
  width: 50%;
  height: 50%;
  pointer-events: none;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  position: absolute;
  top: 18.75%;
  left: 12.5%;
  transform: scale(.3)rotate(65deg);
  box-shadow: 9px 3px #f0f0f0;
}

.moon.visible {
  pointer-events: auto;
  opacity: 1;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  transform: scale(1)rotate(0deg);
}

.star {
  width: 0;
  height: 0;
  opacity: 0;
  border-bottom: 5px solid #f0f0f0;
  border-left: 7px solid rgba(0, 0, 0, 0);
  border-right: 7px solid rgba(0, 0, 0, 0);
  transition: all .2s ease-in .4s;
  display: block;
  position: absolute;
  top: 25%;
  left: 5%;
  transform: scale(.55)rotate(35deg);
}

.star:before {
  height: 0;
  width: 0;
  content: "";
  border-bottom: 5px solid #f0f0f0;
  border-left: 3px solid rgba(0, 0, 0, 0);
  border-right: 3px solid rgba(0, 0, 0, 0);
  display: block;
  position: absolute;
  top: -3px;
  left: -5px;
  transform: rotate(-35deg);
}

.star:after {
  color: red;
  width: 0;
  height: 0;
  content: "";
  border-bottom: 5px solid #f0f0f0;
  border-left: 7px solid rgba(0, 0, 0, 0);
  border-right: 7px solid rgba(0, 0, 0, 0);
  display: block;
  position: absolute;
  top: 0;
  left: -7px;
  transform: rotate(-70deg);
}

.moon.visible .star {
  opacity: .8;
}

.star.small {
  opacity: 0;
  transition: all .2s ease-in .45s;
  position: relative;
  top: 50%;
  left: 37.5%;
  transform: scale(.35)rotate(35deg);
}

.moon.visible .star.small {
  opacity: .7;
  transform: scale(.45)rotate(35deg);
}

